.Searchbar{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height:50px;
    padding: 0 30px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* border-bottom: 4px inset #f7f7f7cc; */
    background-color:rgba(229, 9, 20, 1); 
}
.Searchbar__logoholder{
    width: 10%;
    height: inherit;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.Searchbar__right{
    width: 50%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.glyphicon{
    color: #11111166;
}
.Searchbar__logoholder:hover,.glyphicon:hover { 
    color: rgba(0, 0, 0, 0.8);
}
.glyphicon-list {
    font-size: 25px;
    margin-right: 30px;
}
.glyphicon-search {
    font-size: 15px;
}
.glyphicon-bell{
    font-size: 25px;
    margin-right: 50px;
}
.glyphicon-user{
    font-size: 25px;
}

.btn-default{
    border: 1px solid #11111166 ;
    height: 60%;
    -ms-flex-align: center;
        align-items: center;
    font-family: Roboto, Arial, sans-serif;
    font-size: 16px;
}





